import React, {useEffect, useState} from 'react';
import { connect } from 'react-redux';
import { Divider, Table, Input } from "antd";
import { changLoadingState } from '../../actions/global';
import RecommendedArticles from '../../components/RecommendedArticles';
import { recommendedArticleArr, allBlogLink, blogTypeList } from '../../utils/enums';

const { Search } = Input;

const mapState = state => ({
    loading: state.global.isLoading,
});
function Index(props) {
    const [dataSource, setDataSource] = useState(allBlogLink);
    const columns = [
        {
            title: '序号',
            dataIndex: 'id',
            key: 'id',
            width: 80,
            fixed: 'left',
        }, {
            title: '文章',
            dataIndex: 'name',
            key: 'name',
            render: (text, item) => <a href={item.link} target="_blank">{text}</a>,
        }, {
            title: '分类',
            dataIndex: 'blogType',
            key: 'blogType',
            render: (val) => {
                const typeArr = val.split(',');
                let text = '';
                const len = typeArr.length;
                // 算法优化，抛去无用的计算
                if ((len === 1 && typeArr[0]) || len > 1) {
                    let count = 0;
                    typeArr.some(item => {
                        blogTypeList.some(ite => {
                            if (ite.id == item) {
                                text += `${ite.name},  `;
                                count += 1;
                            }
                            return (ite.id == item);
                        });
                        return (count >= len);
                    })
                }
                return (
                    <div>{text}</div>
                )
            }
        }
    ];
    useEffect(() => {
        props.changLoadingState();
        return () => props.changLoadingState();
    }, [])

    return (
        <div>
            <RecommendedArticles
                recommendedArticleArr={recommendedArticleArr}
            />
            <div
                style={{
                    marginTop: 40,
                    background: '#fff',
                    padding: '10px 58px',
                }}
            >
                <Divider orientation="left">最新文章</Divider>
                <div style={{height: 52}}>
                    <Search
                        placeholder="搜索文章名"
                        onSearch={value => {
                            const newDataSource = allBlogLink.filter(item => (item.name).includes(value));
                            setDataSource(newDataSource)
                        }}
                        style={{
                            width: 200,
                            float: 'right',
                            margin: '10px'
                        }}
                    />
                </div>
                <Table
                    className={'uiTable'}
                    rowKey={'id'}
                    columns={columns}
                    dataSource={dataSource}
                />
            </div>
        </div>
    );
}

export default connect(mapState, { changLoadingState })(Index);



